import Subject from '../../rx/Subject';
import PublisherState from './PublisherState';
import EndPoint, {IStream} from '../discovery/EndPoint';
import assertUnreachable from '../../lang/assertUnreachable';
import DisposableList from '../../lang/DisposableList';
import {PublishStatus} from '../api/Publish';
import Strings from '../../lang/Strings';
import {IRtcPublishMonitorStatistic, IRtcPublishStatistic} from '../../rtc/RtcPublishMonitor';

export default class PublisherContext {
  publisherInitialization: Date;
  disposables: DisposableList;
  publisherDisposables: DisposableList;
  state: Subject<PublisherState>;
  authorized: Subject<boolean>;
  online: Subject<boolean>;
  loading: Subject<boolean>;
  publishing: Subject<boolean>;
  standby: Subject<boolean>;
  stopped: Subject<boolean>;
  failureCount: Subject<number>;
  endPoint: Subject<EndPoint>;
  stream: Subject<IStream>;
  rtcStatistics: Subject<IRtcPublishMonitorStatistic>;
  clearFailureCountTimeout: number;
  name: string;

  rtcAudioStatistic: IRtcPublishStatistic;
  rtcVideoStatistic: IRtcPublishStatistic;

  constructor(name?: string) {
    this.disposables = new DisposableList();
    this.publisherDisposables = new DisposableList();
    this.publisherInitialization = new Date();
    this.state = new Subject<PublisherState>(PublisherState.Starting);
    this.authorized = new Subject<boolean>(true);
    this.online = new Subject<boolean>(true);
    this.loading = new Subject<boolean>(false);
    this.publishing = new Subject<boolean>(false);
    this.standby = new Subject<boolean>(false);
    this.stopped = new Subject<boolean>(false);
    this.failureCount = new Subject<number>(0);
    this.endPoint = new Subject<EndPoint>(null);
    this.stream = new Subject<IStream>(null);
    this.rtcStatistics = new Subject<IRtcPublishMonitorStatistic>(null);
    this.name = name && name.length ? name : this.generateScreenName();
  }

  get streamId(): string {
    const stream = this.stream.value;

    if (!stream) {
      return '-';
    }

    return stream.streamId;
  }

  applyStatus(status: PublishStatus): void {
    switch (status) {
      case 'ok':
        break;
      case 'unauthorized':
      case 'geo-restricted':
      case 'geo-blocked':
        this.authorized.value = false;

      // eslint-disable-next-line no-fallthrough
      case 'no-stream':
      case 'not-found':
        this.failureCount.value = 0;
        this.publishing.value = false;
        this.standby.value = true;
        this.stopped.value = false;
        this.stream.value = null;

        return;
      default:
        this.failureCount.value++;
        this.publishing.value = false;
        this.standby.value = true;
        this.stopped.value = false;
        this.stream.value = null;
        this.state.value = PublisherState.Error;

        return;
    }
  }

  mapPublishStatusToPublisherStatus(status: PublishStatus): PublisherState {
    switch (status) {
      case 'ok':
        return PublisherState.Starting;
      case 'no-stream':
      case 'not-found':
        return PublisherState.StandBy;
      case 'geo-restricted':
        return PublisherState.GeoRestricted;
      case 'geo-blocked':
        return PublisherState.GeoBlocked;
      case 'unauthorized':
        return PublisherState.Unauthorized;
      case 'capacity':
      case 'rate-limited':
      case 'timeout':
        return PublisherState.Recovering;
      case 'failed':
        return PublisherState.Error;
      default:
        assertUnreachable(status);
    }
  }

  private generateScreenName(): string {
    return Strings.random(12);
  }
}