import IPeerConnection from '../../rtc/IPeerConnection';
import SDK from '../SDK';
import EndPoint, {SetRemoteDescriptionStatus} from '../discovery/EndPoint';
import PeerConnectionService, {IPeerConnectionOfferInit} from '../../rtc/PeerConnectionService';
import Discovery from '../discovery/Discovery';
import FeatureEnablement from '../feature/FeatureEnablement';
import PublisherState from './PublisherState';
import assertUnreachable from '../../lang/assertUnreachable';
import {ILogger} from '../../logger/LoggerInterface';
import LoggerFactory from '../../logger/LoggerFactory';
import PublisherContext from './PublisherContext';
import PeerConnectionContext from '../../context/PeerConnectionContext';
import RtcPublishMonitor from '../../rtc/RtcPublishMonitor';
import Durations from '../../time/Duration';
import RtcConfigurationManager from '../../rtc/RtcConfigurationManager';

const iceCandidateAccumulationInterval = 100;

export default class RealTimePublisher {
  private readonly _logger: ILogger = LoggerFactory.getLogger('RealTimePublisher');
  private readonly _publisherContext: PublisherContext;
  private readonly _peerConnectionContext: PeerConnectionContext;
  private readonly _handleStreamFailure: () => Promise<void>;

  constructor(publisherContext: PublisherContext, peerConnectionContext: PeerConnectionContext, handleStreamFailure: () => Promise<void>) {
    this._publisherContext = publisherContext;
    this._peerConnectionContext = peerConnectionContext;
    this._handleStreamFailure = handleStreamFailure;
  }

  start(uri, token, listenOnStreamSetup): Promise<void> {
    return Promise.all<EndPoint, IPeerConnectionOfferInit>([
      Discovery.discoverClosestEndPointWithCaching(uri),
      PeerConnectionService.createPeerConnectionOffer('sendonly')
    ])
      .then(([endPoint, {localOffer, peerConnection}]) => {
        this._publisherContext.online.value = true;
        this._publisherContext.endPoint.value = endPoint;
        this._logger.info('Connecting to [%s]', endPoint.toString());
        this._logger.info('Local offer:\n' + localOffer.sdp);

        if (FeatureEnablement.clientOfferDisabled || !peerConnection.supportsSetConfiguration || !peerConnection.supportsGetConfiguration) {
          peerConnection.close();
          peerConnection = null;
          localOffer = null;
        }

        this._peerConnectionContext.peerConnection.value = peerConnection;

        return endPoint.publish(this._publisherContext.name, token, localOffer, this._publisherContext.failureCount.value);
      })
      .then(({status, stream, rtcConfiguration, setRemoteDescriptionResponse, createOfferDescriptionResponse, createAnswerDescriptionResponse}) => {
        this._publisherContext.stream.value = stream;

        this._logger.debug(
          '[%s] Publish completed [%s] [%j] [%j] [%j] [%j]',
          this._publisherContext.streamId,
          status,
          rtcConfiguration,
          setRemoteDescriptionResponse,
          createOfferDescriptionResponse,
          createAnswerDescriptionResponse
        );

        this._publisherContext.state.value = this._publisherContext.mapPublishStatusToPublisherStatus(status);

        this._publisherContext.applyStatus(status);

        if (status !== 'ok') {
          return;
        }

        return this.applyRtcConfiguration(this._peerConnectionContext.peerConnection.value, rtcConfiguration)
          .then(peerConnection => {
            let submitCandidatesTimeout;
            let cancelDiscovery = false;
            let discoveryCompleted = false;
            const candidates: RTCIceCandidate[] = [];

            this._peerConnectionContext.peerConnection.value = peerConnection;

            peerConnection.addStream(this._peerConnectionContext.mediaStream.value);

            peerConnection.onicecandidate = (e): void => {
              if (this._publisherContext.stream.value !== stream) {
                return;
              }

              if (this._peerConnectionContext.peerConnection.value !== peerConnection) {
                return;
              }

              if (cancelDiscovery) {
                return;
              }

              if (!SDK.sendLocalCandidates.value) {
                return;
              }

              if (e.candidate && e.candidate.candidate) {
                candidates.push(e.candidate);
              } else {
                discoveryCompleted = true;
              }

              if (!submitCandidatesTimeout) {
                submitCandidatesTimeout = setTimeout(() => {
                  if (this._publisherContext.stream.value !== stream) {
                    return;
                  }

                  if (cancelDiscovery) {
                    return;
                  }

                  const ignored = this._publisherContext.endPoint.value.addIceCandidates(stream, candidates, discoveryCompleted)
                    .then(({status, options}) => {
                      if (status !== 'ok') {
                        this._logger.warn('[%s] Failed to add ICE candidates with reason [%s]', this._publisherContext.streamId, status);

                        return;
                      }

                      if (options.includes('cancel')) {
                        cancelDiscovery = true;
                      }

                      this._logger.info('[%s] Added ICE candidates with reason [%s] and options [%s]', this._publisherContext.streamId, status, options);
                    })
                    .catch(e => {
                      this._logger.error('[%s] Failed to add ICE candidates', this._publisherContext.streamId, e);
                    });
                }, iceCandidateAccumulationInterval);
              }
            };

            peerConnection.oniceconnectionstatechange = (): void => {
              if (this._publisherContext.stream.value !== stream) {
                return;
              }

              if (this._peerConnectionContext.peerConnection.value !== peerConnection) {
                return;
              }

              const retryCallback = (): void => {
                // If we stop normally the peer connection is unregistered first.
                // Thus anytime we see a closed peer connection that is still valid, it is an error.
                this._publisherContext.state.value = PublisherState.Error;
                this._publisherContext.publishing.value = false;
                this._publisherContext.loading.value = true;

                const ignored = this._handleStreamFailure()
                  .catch(e => {
                    this._logger.error(
                      '[%s] Failed handling stream failure after peer connection stopped with state [%s]',
                      this._publisherContext.streamId,
                      peerConnection.iceConnectionState,
                      e
                    );
                  });
              };

              switch (peerConnection.iceConnectionState) {
                case 'checking':
                case 'completed':
                case 'connected':
                case 'new':
                  return;

                case 'disconnected':
                case 'failed':
                  if (navigator.onLine) {
                    this._logger.info('[%s] ICE connection state changed to [%s], trying to reconnect', this._publisherContext.streamId, peerConnection.iceConnectionState);
                    this.reconnectPeerConnection(peerConnection, retryCallback);
                  }

                  return;
                case 'closed':
                  this._logger.info('[%s] ICE connection state changed to [%s], retrying to connect', this._publisherContext.streamId, peerConnection.iceConnectionState);
                  retryCallback();

                  return;
                default:
                  assertUnreachable(peerConnection.iceConnectionState);
              }
            };

            return new Promise(resolve => {
              resolve();
            }).then(() => {
              if (!setRemoteDescriptionResponse) {
                return;
              }

              this._logger.info('[%s] Set local SDP offer [%s]', this._publisherContext.streamId, setRemoteDescriptionResponse.sessionDescription.sdp);

              return peerConnection.setLocalDescription(setRemoteDescriptionResponse.sessionDescription)
                .catch(e => {
                  this._logger.info('[%s] Failed to set local description [%j] with message [%s]', this._publisherContext.streamId, setRemoteDescriptionResponse.sessionDescription, e.message);

                  throw e;
                });
            }).then(() => {
              if (!createAnswerDescriptionResponse) {
                return;
              }

              this._logger.info('[%s] Set remote SDP answer [%s]', this._publisherContext.streamId, createAnswerDescriptionResponse.sessionDescription.sdp);

              return peerConnection.setRemoteDescription(createAnswerDescriptionResponse.sessionDescription)
                .catch(e => {
                  this._logger.info('[%s] Failed to set remote description [%j] with message [%s]', this._publisherContext.streamId, createAnswerDescriptionResponse.sessionDescription, e.message);

                  throw e;
                });
            }).then(() => {
              if (!createOfferDescriptionResponse) {
                return;
              }

              this._logger.info('[%s] Set remote SDP offer [%s]', this._publisherContext.streamId, createOfferDescriptionResponse.sessionDescription.sdp);

              return peerConnection.setRemoteDescription(createOfferDescriptionResponse.sessionDescription)
                .catch(e => {
                  this._logger.info('[%s] Failed to set remote description [%j] with message [%s]', this._publisherContext.streamId, createAnswerDescriptionResponse.sessionDescription, e.message);

                  throw e;
                })
                .then(() => {
                  return peerConnection.createAnswer({
                    offerToReceiveAudio: false,
                    offerToReceiveVideo: false
                  });
                }).then(answer => {
                  this._logger.info('[%s] Set local SDP answer [%s]', this._publisherContext.streamId, answer.sdp);

                  return this._publisherContext.endPoint.value.setRemoteDescription(stream, answer);
                }).then(({status, sessionDescription}) => {
                  this._publisherContext.state.value = this.mapSetRemoteDescriptionStatusToPublisherStatus(status);

                  if (status !== 'ok') {
                    this._publisherContext.publishing.value = false;
                    this._publisherContext.standby.value = true;
                    this._publisherContext.stopped.value = false;

                    return;
                  }

                  this._logger.info('[%s] Set local SDP [%s]', this._publisherContext.streamId, sessionDescription.sdp);

                  return peerConnection.setLocalDescription(sessionDescription)
                    .catch(e => {
                      this._logger.info('[%s] Failed to set local description [%j] with message [%s]', this._publisherContext.streamId, sessionDescription, e.message);

                      throw e;
                    });
                });
            }).then(() => {
              listenOnStreamSetup.success(this._publisherContext.streamId);

              this._publisherContext.state.value = PublisherState.Publishing;
              this._publisherContext.publishing.value = true;

              const rtcPublishMonitor = new RtcPublishMonitor(peerConnection, this._peerConnectionContext.mediaStream.value, this._publisherContext.endPoint.value.roundTripTime / 4);
              const ignored = rtcPublishMonitor.rtcStatistic.subscribe(statistics => {
                this._publisherContext.rtcStatistics.value = statistics;

                if (!this._publisherContext.rtcVideoStatistic && !this._publisherContext.rtcAudioStatistic) {
                  this._publisherContext.rtcAudioStatistic = statistics.audio;
                  this._publisherContext.rtcVideoStatistic = statistics.video;

                  return;
                }

                let audioTrackFailed = false;
                let videoTrackFailed = false;

                if (statistics.audio) {
                  if (this._publisherContext.rtcAudioStatistic && this._publisherContext.rtcAudioStatistic.timestamp !== statistics.audio.timestamp) {
                    audioTrackFailed = this._publisherContext.rtcAudioStatistic && this._publisherContext.rtcAudioStatistic.bytesSent === statistics.audio.bytesSent;

                    if (audioTrackFailed && navigator.onLine) {
                      this._logger.info(
                        '[%s] Audio track failed with last bytesSent [%s] is equal to previous bytesSent [%s] within [%s]',
                        this._publisherContext.streamId,
                        statistics.audio.bytesSent,
                        this._publisherContext.rtcAudioStatistic.bytesSent,
                        new Durations(statistics.audio.timestamp - this._publisherContext.rtcAudioStatistic.timestamp).toIsoString()
                      );
                    }

                    this._publisherContext.rtcAudioStatistic = statistics.audio;
                  }
                }

                if (statistics.video) {
                  if (this._publisherContext.rtcVideoStatistic && this._publisherContext.rtcVideoStatistic.timestamp !== statistics.video.timestamp) {
                    videoTrackFailed = this._publisherContext.rtcVideoStatistic && this._publisherContext.rtcVideoStatistic.bytesSent === statistics.video.bytesSent;

                    if (videoTrackFailed && navigator.onLine) {
                      this._logger.info(
                        '[%s] Video track failed with last bytesSent [%s] is equal to previous bytesSent [%s] within [%s]',
                        this._publisherContext.streamId,
                        statistics.video.bytesSent,
                        this._publisherContext.rtcVideoStatistic.bytesSent,
                        new Durations(statistics.video.timestamp - this._publisherContext.rtcVideoStatistic.timestamp).toIsoString()
                      );
                    }

                    this._publisherContext.rtcVideoStatistic = statistics.video;
                  }
                }

                if ((videoTrackFailed || audioTrackFailed) && navigator.onLine) {
                  const retryCallback = (): void => {
                    this._publisherContext.state.value = PublisherState.Error;
                    this._publisherContext.loading.value = true;

                    rtcPublishMonitor.dispose();

                    const ignored = this._handleStreamFailure()
                      .catch(e => {
                        this._logger.error(
                          '[%s] Failed handling stream failure after track stopped with state [%s]',
                          this._publisherContext.streamId,
                          peerConnection.iceConnectionState,
                          e
                        );
                      });
                  };

                  this.reconnectPeerConnection(peerConnection, retryCallback);
                } else {
                  this._peerConnectionContext.peerConnectionReconnectAttempts = 0;
                }
              });

              return Promise.resolve();
            });
          });
      });
  }

  private async applyRtcConfiguration(
    optionalPeerConnection: IPeerConnection | null,
    rtcConfiguration: RTCConfiguration): Promise<IPeerConnection> {
    if (!optionalPeerConnection) {
      rtcConfiguration = RtcConfigurationManager.truncateIceServers(rtcConfiguration);

      return SDK.peerConnectionFactory.value.createPeerConnection(rtcConfiguration);
    }

    const newRtcConfiguration = {
      ...optionalPeerConnection.getConfiguration(),
      ...rtcConfiguration
    };

    optionalPeerConnection.setConfiguration(newRtcConfiguration);

    return optionalPeerConnection;
  }

  private reconnectPeerConnection(peerConnection: IPeerConnection, retryCallback: () => void): void {
    if (peerConnection.iceConnectionState === 'closed') {
      return;
    }

    this._publisherContext.state.value = PublisherState.Reconnecting;

    if (this._peerConnectionContext.peerConnectionReconnectAttempts < SDK.maximalNumberOfPeerConnectionReconnectAttempts || !SDK.automaticallyReconnectPeerConnection) {
      this._peerConnectionContext.peerConnectionReconnectAttempts++;

      if (FeatureEnablement.clientOfferDisabled ||
        !peerConnection.supportsSetConfiguration ||
        !peerConnection.supportsGetConfiguration ||
        this._publisherContext.failureCount.value
      ) {
        return;
      }

      this._logger.info('Reconnecting peer connection by restarting ICE');

      const localOffer = peerConnection.currentLocalDescription;
      const ignored = peerConnection.createOffer({iceRestart: true}).then(offer => {
        return peerConnection.setLocalDescription(offer).then(() => {
          return peerConnection.setLocalDescription(localOffer)
            .then(() => {
              this._publisherContext.state.value = PublisherState.Publishing;
            });
        });
      })
        .catch(e => {
          this._logger.error('Failed to reconnect peer connection', e);
          this._peerConnectionContext.peerConnectionReconnectAttempts = 0;
          retryCallback();
        });

      return;
    }

    this._logger.info('Failed to reconnect peer connection after [%s] attempts, performing full recovery', this._peerConnectionContext.peerConnectionReconnectAttempts);
    this._peerConnectionContext.peerConnectionReconnectAttempts = 0;
    retryCallback();
  }

  private mapSetRemoteDescriptionStatusToPublisherStatus(status: SetRemoteDescriptionStatus): PublisherState {
    switch (status) {
      case 'ok':
        return PublisherState.Starting;
      case 'unauthorized':
        return PublisherState.Unauthorized;
      case 'not-found':
      case 'capacity':
      case 'rate-limited':
      case 'timeout':
        return PublisherState.Recovering;
      case 'failed':
        return PublisherState.Error;
      default:
        assertUnreachable(status);
    }
  }
}