import IPeerConnection from './IPeerConnection';
import Subject from '../rx/Subject';
import FeatureEnablement from '../sdk/feature/FeatureEnablement';
import SdpParser from './SdpParser';
import IDisposable from '../lang/IDisposable';

export interface IRtcPublishMonitorStatistic {
  [kind: string]: IRtcPublishStatistic;
}

export interface ILegacyRTCStatsReport {
  result: () => ILegacyRTCStatsReportItem[];
}

export interface ILegacyRTCStatsReportItem extends RTCStatsReport {
  type: string;
  names: () => string[];
  stat: (name) => number;
}

export interface IRtcPublishStatistic {
  lastPacketSentTimestamp?: number;
  ssrc: string;
  mediaType: string;
  timestamp: number;
  bitrate?: number;
  bytesSent: number;
  packetsSent: number;
  retransmittedBytesSent?: number;
  firCount?: number;
  frameHeight?: number;
  frameWidth?: number;
  framesEncoded?: number;
  framesSent?: number;
  headerBytesSent?: number;
  hugeFramesSent?: number;
  pliCount?: number;
  qpSum?: number;
  totalEncodeTime?: number;
  totalEncodedBytesTarget?: number;
  codec: string;
  fps?: number;
  roundTripTime: number;
}

const updateStatisticTimeOut = 1500;

export default class RtcPublishMonitor implements IDisposable {
  private readonly _estimatedRoundTripTime: number;
  private readonly _estimatedVideoCodec: string;
  private readonly _estimatedAudioCodec: string;
  private readonly _rtcStatistic: Subject<IRtcPublishMonitorStatistic> = new Subject<IRtcPublishMonitorStatistic>({});
  private _peerConnection: IPeerConnection;
  private _isMonitorRunning = true;
  private _updateTimeOut: number;
  private _availableTracks: {
    audio: boolean;
    video: boolean;
  }

  constructor(peerConnection: IPeerConnection, mediaStream: MediaStream, estimatedRoundTripTime: number) {
    this._peerConnection = peerConnection;
    this._estimatedRoundTripTime = estimatedRoundTripTime;

    if (this._peerConnection) {
      const parsedLocalDescription = new SdpParser(this._peerConnection.currentLocalDescription.sdp);
      const parsedRemoteDescription = new SdpParser(this._peerConnection.currentRemoteDescription.sdp);

      this._estimatedAudioCodec = parsedRemoteDescription.audioCodec;
      this._estimatedVideoCodec = parsedRemoteDescription.videoCodec;

      this._availableTracks = {
        audio: parsedLocalDescription.isAudioTrackEnabled && parsedRemoteDescription.isAudioTrackEnabled,
        video: parsedLocalDescription.isVideoTrackEnabled && parsedRemoteDescription.isVideoTrackEnabled
      };

      this.updateStatistic();
    }
  }

  get rtcStatistic(): Subject<IRtcPublishMonitorStatistic> {
    return this._rtcStatistic;
  }

  dispose(): void {
    this._isMonitorRunning = false;
    this._peerConnection = null;

    if (this._updateTimeOut) {
      clearTimeout(this._updateTimeOut);
      this._updateTimeOut = null;
    }
  }

  private async updateStatistic(): Promise<void> {
    if (FeatureEnablement.getStatsPromiseBasedDisabled) {
      const ignored = {
        audio: 0,
        video: 0
      };

      return;
    }

    // Safari 11 doesnt have kind or mediaType so we need to take it from the inbound-rtp object id
    const getKindForSafari11 = (item): string => item.id.includes('Audio') ? 'audio' : item.id.includes('Video') ? 'video' : '';
    const ignored = await this._peerConnection.getStats(null)
      .then(stats => {
        const rtcStatistics: IRtcPublishMonitorStatistic = {};

        if (stats) {
          let roundTripTime = this._estimatedRoundTripTime;
          let lastPacketSentTimestamp;

          stats.forEach(report => {
            if (report.type === 'candidate-pair' && report.currentRoundTripTime) {
              roundTripTime = report.currentRoundTripTime * 1000;

              if (rtcStatistics['audio']) {
                rtcStatistics['audio'].roundTripTime = roundTripTime;
              }

              if (rtcStatistics['video']) {
                rtcStatistics['video'].roundTripTime = roundTripTime;
              }
            }

            if (report.type === 'candidate-pair' && report.lastPacketSentTimestamp) {
              lastPacketSentTimestamp = report.lastPacketSentTimestamp;

              if (rtcStatistics['audio']) {
                rtcStatistics['audio'].lastPacketSentTimestamp = lastPacketSentTimestamp;
              }

              if (rtcStatistics['video']) {
                rtcStatistics['video'].lastPacketSentTimestamp = lastPacketSentTimestamp;
              }
            }

            if (report.type === 'outbound-rtp') {
              const kind = report.kind || report.mediaType || getKindForSafari11(report);
              let codec = '';

              stats.forEach(item => {
                if (item.id === report.codecId) {
                  codec = item.mimeType;
                }
              });

              if (kind && this._availableTracks[kind]) {
                const bytes = report.bytesSent || 0;
                const previousBytesSent = this._rtcStatistic.value?.[kind]?.bytesSent ?? 0;
                const bytesDelta = bytes - previousBytesSent;
                const timeDelta = report.timestamp - this._rtcStatistic.value?.[kind]?.timestamp;
                const bitrate = bytesDelta && timeDelta ? Math.floor(8 * bytesDelta /
                  timeDelta * 1000) : 0;

                rtcStatistics[kind] = {
                  ssrc: report.ssrc,
                  mediaType: kind,
                  timestamp: report.timestamp,
                  bitrate,
                  bytesSent: report.bytesSent,
                  packetsSent: report.packetsSent,
                  retransmittedBytesSent: report.retransmittedBytesSent,
                  codec: codec || this.getCodecByType(kind),
                  roundTripTime
                };

                if (report.lastPacketSentTimestamp || lastPacketSentTimestamp) {
                  rtcStatistics[kind].lastPacketSentTimestamp = report.lastPacketSentTimestamp || lastPacketSentTimestamp;
                }

                if (kind === 'video' && this._rtcStatistic.value && this._rtcStatistic.value.video) {
                  rtcStatistics[kind]['firCount'] = report.firCount;
                  rtcStatistics[kind]['frameHeight'] = report.frameHeight;
                  rtcStatistics[kind]['frameWidth'] = report.frameWidth;
                  rtcStatistics[kind]['framesEncoded'] = report.framesEncoded;
                  rtcStatistics[kind]['framesSent'] = report.framesSent;
                  rtcStatistics[kind]['headerBytesSent'] = report.headerBytesSent;
                  rtcStatistics[kind]['hugeFramesSent'] = report.hugeFramesSent;
                  rtcStatistics[kind]['pliCount'] = report.pliCount;
                  rtcStatistics[kind]['qpSum'] = report.qpSum;
                  rtcStatistics[kind]['totalEncodeTime'] = report.totalEncodeTime;
                  rtcStatistics[kind]['totalEncodedBytesTarget'] = report.totalEncodedBytesTarget;

                  const fps = (report.framesEncoded - this._rtcStatistic.value.video.framesEncoded) /
                    (rtcStatistics[kind].timestamp - this._rtcStatistic.value.video.timestamp) *
                    1000;

                  rtcStatistics[kind]['fps'] = fps ? Math.round(fps * 100) / 100 : 0;
                }
              }
            }
          });
        }

        this._rtcStatistic.value = rtcStatistics;
      }).then(() => {
        if (this._isMonitorRunning) {
          this._updateTimeOut = window.setTimeout(() => this.updateStatistic(), updateStatisticTimeOut);
        }
      });
  }

  private getCodecByType(type: string): string {
    switch (type) {
      case 'audio':
        return this._estimatedAudioCodec;
      case 'video':
        return this._estimatedVideoCodec;
      default:
        return 'unknown';
    }
  }
}