import LoggerFactory from '../../logger/LoggerFactory';
import IDisposable from '../../lang/IDisposable';
import ReadOnlySubject from '../../rx/ReadOnlySubject';
import EndPoint, {IStream} from '../discovery/EndPoint';
import SDK from '../SDK';
import IPeerConnection from '../../rtc/IPeerConnection';
import PublisherState from './PublisherState';
import assertUnreachable from '../../lang/assertUnreachable';
import Disposable from '../../lang/Disposable';
import EdgeAuth from '../edgeAuth/EdgeAuth';
import {ILogger} from '../../logger/LoggerInterface';
import SessionTelemetry from '../../video/SessionTelemetry';
import {IRtcMonitorStatistic} from '../../rtc/RtcConnectionMonitor';

import {EdgeToken} from '../edgeAuth/EdgeToken';
import TokenContext from '../../context/TokenContext';
import PublisherContext from './PublisherContext';
import PeerConnectionContext from '../../context/PeerConnectionContext';
import StateContext from '../../context/StateContext';
import DiscoveryUri from '../discovery/DiscoveryUri';
import MetricsFactory from '../../metrics/MetricsFactory';
import MetricsService from '../../metrics/MetricsService';
import RealTimePublisher from './RealTimePublisher';

const defaultStreamTerminationReason = 'client:termination';
const backoffIntervalInMilliseconds = 2000;
const failureCountCleanUpIntervalInMilliseconds = 3000;
const maxBackoffIntervalInMilliseconds = 300000;
const standbyPollingIntervalInMilliseconds = 15000;

export default class Publisher implements IDisposable {
  private readonly _logger: ILogger = LoggerFactory.getLogger('Publisher');
  private readonly _tokenContext: TokenContext;
  private readonly _publisherContext: PublisherContext;
  private readonly _peerConnectionContext: PeerConnectionContext;
  private readonly _stateContext: StateContext;
  private readonly _publisherStartTime: number;
  private readonly _readOnlyToken: ReadOnlySubject<EdgeToken>;
  private readonly _readOnlyPeerConnection: ReadOnlySubject<IPeerConnection>;
  private readonly _readOnlyState: ReadOnlySubject<PublisherState>;
  private readonly _readOnlyTokenExpiring: ReadOnlySubject<boolean>;
  private readonly _readOnlyAuthorized: ReadOnlySubject<boolean>;
  private readonly _readOnlyOnline: ReadOnlySubject<boolean>;
  private readonly _readOnlyLoading: ReadOnlySubject<boolean>;
  private readonly _readOnlyPublishing: ReadOnlySubject<boolean>;
  private readonly _readOnlyStandby: ReadOnlySubject<boolean>;
  private readonly _readOnlyStopped: ReadOnlySubject<boolean>;
  private readonly _readOnlyFailureCount: ReadOnlySubject<number>;
  private readonly _readOnlyEndPoint: ReadOnlySubject<EndPoint>;
  private readonly _readOnlyStream: ReadOnlySubject<IStream>;
  private readonly _readOnlyRtcStatistics: ReadOnlySubject<IRtcMonitorStatistic>;
  private readonly _readOnlyMediaStream: ReadOnlySubject<MediaStream>;

  private _metricsService: MetricsService;
  private readonly _sessionTelemetry: SessionTelemetry;

  constructor(mediaStream: MediaStream, token: string, name?: string) {
    this._tokenContext = new TokenContext(token);
    this._publisherContext = new PublisherContext(name);
    this._peerConnectionContext = new PeerConnectionContext();
    this._stateContext = new StateContext();
    this._publisherStartTime = Date.now();
    this._readOnlyToken = new ReadOnlySubject<string>(this._tokenContext.token);
    this._readOnlyPeerConnection = new ReadOnlySubject<IPeerConnection>(this._peerConnectionContext.peerConnection);
    this._readOnlyState = new ReadOnlySubject<PublisherState>(this._publisherContext.state);
    this._readOnlyTokenExpiring = new ReadOnlySubject<boolean>(this._tokenContext.tokenExpiring);
    this._readOnlyAuthorized = new ReadOnlySubject<boolean>(this._publisherContext.authorized);
    this._readOnlyOnline = new ReadOnlySubject<boolean>(this._publisherContext.online);
    this._readOnlyLoading = new ReadOnlySubject<boolean>(this._publisherContext.loading);
    this._readOnlyPublishing = new ReadOnlySubject<boolean>(this._publisherContext.publishing);
    this._readOnlyStandby = new ReadOnlySubject<boolean>(this._publisherContext.standby);
    this._readOnlyStopped = new ReadOnlySubject<boolean>(this._publisherContext.stopped);
    this._readOnlyFailureCount = new ReadOnlySubject<number>(this._publisherContext.failureCount);
    this._readOnlyEndPoint = new ReadOnlySubject<EndPoint>(this._publisherContext.endPoint);
    this._readOnlyStream = new ReadOnlySubject<IStream>(this._publisherContext.stream);
    this._readOnlyRtcStatistics = new ReadOnlySubject<IRtcMonitorStatistic>(this._publisherContext.rtcStatistics);
    this._readOnlyMediaStream = new ReadOnlySubject<MediaStream>(this._peerConnectionContext.mediaStream);

    this._peerConnectionContext.mediaStream.value = mediaStream;

    const parsedToken = EdgeAuth.parseToken(this._tokenContext.token.value);
    const discoveryUri = (EdgeAuth.getUri(parsedToken) || SDK.discoveryUri.value).toString();

    SDK.tenancy.value = EdgeAuth.getTenancy(parsedToken) || SDK.tenancy.value;
    DiscoveryUri.uri.value = discoveryUri;
    this._metricsService = MetricsFactory.getMetricsService(discoveryUri);
    this._sessionTelemetry = new SessionTelemetry(SDK.pageLoadTime, this._metricsService);
    this._publisherContext.publisherDisposables.add(this._sessionTelemetry);

    this._publisherContext.publisherDisposables.add(
      this._publisherContext.state.subscribe(state => {
        if (this._publisherContext.clearFailureCountTimeout) {
          clearTimeout(this._publisherContext.clearFailureCountTimeout);
        }

        if (!this._publisherContext.failureCount.value) {
          return;
        }

        if (state !== PublisherState.Publishing) {
          return;
        }

        this._publisherContext.clearFailureCountTimeout = window.setTimeout(() => {
          this._publisherContext.failureCount.value = 0;
        }, failureCountCleanUpIntervalInMilliseconds);
      }));

    const destroyStreamOnUnmount = () => {
      if (this._publisherContext.stream.value && this._publisherContext.endPoint.value) {
        const ignored = this._publisherContext.endPoint.value.destroyStream(this._publisherContext.stream.value, 'client:termination-on-window-unload');
      }
    };

    window.addEventListener('beforeunload', destroyStreamOnUnmount);

    this._publisherContext.publisherDisposables.add(new Disposable(() => {
      window.removeEventListener('beforeunload', destroyStreamOnUnmount);
    }));

    this.start();
  }

  get token(): EdgeToken {
    return this._tokenContext.token.value;
  }

  set token(token: EdgeToken) {
    this._publisherContext.disposables.dispose();

    this._tokenContext.token.value = token;
    this._tokenContext.tokenExpiring.value = false;

    const parsedToken = EdgeAuth.parseToken(this._tokenContext.token.value);
    const discoveryUri = (EdgeAuth.getUri(parsedToken) || SDK.discoveryUri.value).toString();

    SDK.tenancy.value = EdgeAuth.getTenancy(parsedToken) || SDK.tenancy.value;
    DiscoveryUri.uri.value = discoveryUri;

    this._metricsService = MetricsFactory.getMetricsService(discoveryUri);

    this.start();
  }

  get peerConnection(): ReadOnlySubject<IPeerConnection> {
    return this._readOnlyPeerConnection;
  }

  get state(): ReadOnlySubject<PublisherState> {
    return this._readOnlyState;
  }

  get tokenExpiring(): ReadOnlySubject<boolean> {
    return this._readOnlyTokenExpiring;
  }

  get authorized(): ReadOnlySubject<boolean> {
    return this._readOnlyAuthorized;
  }

  get online(): ReadOnlySubject<boolean> {
    return this._readOnlyOnline;
  }

  get loading(): ReadOnlySubject<boolean> {
    return this._readOnlyLoading;
  }

  get publishing(): ReadOnlySubject<boolean> {
    return this._readOnlyPublishing;
  }

  get standby(): ReadOnlySubject<boolean> {
    return this._readOnlyStandby;
  }

  get stopped(): ReadOnlySubject<boolean> {
    return this._readOnlyStopped;
  }

  get failureCount(): ReadOnlySubject<number> {
    return this._readOnlyFailureCount;
  }

  get endPoint(): ReadOnlySubject<EndPoint> {
    return this._readOnlyEndPoint;
  }

  get stream(): ReadOnlySubject<IStream> {
    return this._readOnlyStream;
  }

  get streamId(): string {
    return this._publisherContext.streamId;
  }

  get rtcStatistics(): ReadOnlySubject<IRtcMonitorStatistic> {
    return this._readOnlyRtcStatistics;
  }

  get mediaStream(): ReadOnlySubject<MediaStream> {
    return this._readOnlyMediaStream;
  }

  async stop(reason: string): Promise<void> {
    return new Promise(resolve => {
      if (!this._stateContext.isStarting.value) {
        this.processStop(reason);

        resolve();

        return;
      }

      this._publisherContext.disposables.add(this._stateContext.isStarting.subscribe(isStarting => {
        if (!isStarting) {
          this.processStop(reason);
          resolve();
        }
      }));
    });
  }

  private processStop(reason: string): void {
    this.cleanUpResources(reason);

    if (this._peerConnectionContext.peerConnection.value) {
      this._peerConnectionContext.peerConnection.value.close();
      this._peerConnectionContext.peerConnection.value = null;
    }

    this._publisherContext.state.value = PublisherState.Stopped;
  }

  async dispose(): Promise<void> {
    return this.stop('client:channel-dispose').then(() => {
      this._publisherContext.publisherDisposables.dispose();
      this._stateContext.isDisposed = true;
    });
  }

  getUri(token): URL {
    const parsedToken = EdgeAuth.parseToken(token);
    const url = EdgeAuth.getUri(parsedToken);

    if (url) {
      return url;
    }

    this._logger.info('Fall back to the default discover URI [%s]', SDK.discoveryUri.value);

    return new URL(SDK.discoveryUri.value);
  }

  async start(): Promise<void> {
    if (this._stateContext.isDisposed) {
      throw new Error('Channel was already disposed');
    }

    if (this._stateContext.isStarting.value) {
      this._logger.info('Publisher is already starting, skipping start');

      return;
    }

    this._stateContext.isStarting.value = true;

    return this.processStart();
  }

  setMediaStream(mediaStream: MediaStream) {
    this._peerConnectionContext.mediaStream.value = mediaStream;

    if (this._peerConnectionContext.peerConnection.value) {
      mediaStream.getTracks().forEach(mediaTrack => {
        this._peerConnectionContext.peerConnection.value.getSenders().forEach(sender => {
          if (mediaTrack.kind === sender?.track?.kind && mediaTrack.id !== sender.track.id) {
            sender.replaceTrack(mediaTrack);
          }
        });
      });
    }
  }

  private async processStart(): Promise<void> {
    const token = this._tokenContext.token.value;
    const listenOnStreamSetup = this._sessionTelemetry.listenOnStreamSetup();

    if (!EdgeAuth.isValidToken(token)) {
      this._logger.error('Failed to parse token [%s]', token);
      this._publisherContext.state.value = PublisherState.Unauthorized;
      this._publisherContext.authorized.value = false;
      this._stateContext.isStarting.value = false;

      return;
    }

    this.cleanUpResources('client:start');
    this._publisherContext.state.value = PublisherState.Starting;
    this._publisherContext.loading.value = true;

    const uri = this.getUri(token);
    const handleStreamFailureCallback: () => Promise<void> = () => new Promise((resolve): void => {
      // Need to set isStarting to false and call handleStreamFailure if stream monitors found an issue
      this._stateContext.isStarting.value = false;

      return resolve(this.handleStreamFailure());
    });
    const streamPublisher = new RealTimePublisher(this._publisherContext, this._peerConnectionContext, handleStreamFailureCallback);

    return streamPublisher.start(
      uri,
      token,
      listenOnStreamSetup)
      .then(() => {
        this._publisherContext.loading.value = false;
      })
      .catch(e => {
        listenOnStreamSetup.fail();

        this._publisherContext.failureCount.value++;

        this._publisherContext.online.value = false;

        this.cleanUpResources('client:cleanup-after-failed-setup');

        this._publisherContext.state.value = PublisherState.Error;

        this._logger.error('Failed to start publishing', e);
      })
      .finally(() => {
        this._stateContext.isStarting.value = false;

        if (this._publisherContext.state.value === PublisherState.Publishing || !SDK.automaticRetryOnFailure) {
          return;
        }

        const timeoutId = setTimeout(() => {
          const ignored = this.handleStreamFailure()
            .catch(e => {
              this._logger.error('Failed handling stream failure', e);
            });
        }, this.getRetryInterval());

        this._publisherContext.disposables.add(new Disposable(() => {
          clearTimeout(timeoutId);
        }));
      });
  }

  private getRetryInterval(): number {
    switch (this._publisherContext.state.value) {
      case PublisherState.StandBy:
      case PublisherState.Offline:
        return standbyPollingIntervalInMilliseconds;
      case PublisherState.Error:
      case PublisherState.Recovering:
      case PublisherState.Unauthorized:
      case PublisherState.GeoRestricted:
      case PublisherState.GeoBlocked:
      case PublisherState.Stopped:
      case PublisherState.Starting:
      case PublisherState.Publishing:
      case PublisherState.Reconnecting:
      case PublisherState.UnsupportedFeature:
        // First and second attempt fast, after that exponential with backoff interval
        return Math.min(maxBackoffIntervalInMilliseconds, Math.pow(backoffIntervalInMilliseconds, Math.max(0, this._publisherContext.failureCount.value - 1)));
      default:
        assertUnreachable(this._publisherContext.state.value);
    }
  }

  private async handleStreamFailure(): Promise<void> {
    switch (this._publisherContext.state.value) {
      case PublisherState.Error:
      case PublisherState.Reconnecting:
      case PublisherState.StandBy:
      case PublisherState.Offline:
      case PublisherState.Recovering:
        this._logger.info('Retry start with initial state [%s] [%s]', this._publisherContext.state.value, PublisherState[this._publisherContext.state.value]);

        break;
      case PublisherState.Unauthorized:
        this._logger.info('Publisher is unauthorized, skipping retry of start. Please provide a new token and invoke start()');

        return;
      case PublisherState.GeoRestricted:
        this._logger.info('Publisher is geo restricted, skipping retry of start. Please provide a new token and invoke start()');

        return;
      case PublisherState.GeoBlocked:
        this._logger.info('Publisher is geo blocked, skipping retry of start. Please provide a new token and invoke start()');

        return;
      case PublisherState.Stopped:
        this._logger.info('Publisher is stopped, skipping retry of start.');

        return;
      case PublisherState.Publishing:
        this._logger.info('Publisher is publishing, skipping retry of start');

        return;
      case PublisherState.Starting:
        this._logger.info('Publisher is already starting, skipping retry of start');

        return;
      case PublisherState.UnsupportedFeature:
        this._logger.info('Publisher is stopped due to unsupported feature, skipping retry of start.');

        return;
      default:
        assertUnreachable(this._publisherContext.state.value);
    }

    return this.start();
  }

  private cleanUpResources(reason: string = defaultStreamTerminationReason): void {
    this._publisherContext.disposables.dispose();

    const peerConnection = this._peerConnectionContext.peerConnection.value;

    if (peerConnection) {
      this._peerConnectionContext.peerConnection.value = null;
      peerConnection.close();
    }

    this._publisherContext.publishing.value = false;
    this._publisherContext.stopped.value = true;
    this._publisherContext.standby.value = false;

    if (this._publisherContext.stream.value && this._publisherContext.endPoint.value) {
      const ignored = this._publisherContext.endPoint.value.destroyStream(this._publisherContext.stream.value, reason)
        .then(({status}) => {
          if (status !== 'ok') {
            this._logger.warn('[%s] Failed to destroy stream with reason [%s]', this.streamId, status);

            return;
          }

          this._logger.info('[%s] Destroyed stream with reason [%s]', this.streamId, status);
        })
        .catch(e => {
          this._logger.error('[%s] Failed to destroy stream', this.streamId, e);
        });
    }

    this._publisherContext.stream.value = null;
    this._publisherContext.endPoint.value = null;
    this._peerConnectionContext.peerConnectionReconnectAttempts = 0;
  }
}